/* Thunder Client 스타일 시스템 for HAPA Landing Page */

:root {
  /* HAPA 브랜드 색상 (Thunder Client 스타일 적용) */
  --hapa-primary: #007acc;
  --hapa-primary-hover: #0e639c;
  --hapa-primary-dark: #005a9e;
  --hapa-secondary: #40a9ff;
  --hapa-accent: #4caf50;
  --hapa-purple: #924e96; /* Thunder Client 원본 색상 */

  /* 그라디언트 */
  --hapa-gradient: linear-gradient(135deg, #007acc 0%, #0e639c 100%);
  --hapa-gradient-hover: linear-gradient(135deg, #0e639c 0%, #1177bb 100%);
  --hapa-gradient-purple: linear-gradient(135deg, #924e96 0%, #7a4282 100%);

  /* 섀도우 */
  --thunder-shadow-sm: 0 1px 3px rgba(0, 0, 0, 0.12);
  --thunder-shadow-md: 0 2px 6px rgba(0, 0, 0, 0.15);
  --thunder-shadow-lg: 0 4px 12px rgba(0, 0, 0, 0.18);

  /* 색상 시스템 */
  --thunder-bg-primary: #ffffff;
  --thunder-bg-secondary: #f8f9fa;
  --thunder-bg-dark: #1e1e1e;
  --thunder-bg-section: #f5f5f5;

  --thunder-text-primary: #333333;
  --thunder-text-secondary: #666666;
  --thunder-text-muted: #999999;
  --thunder-text-light: #ffffff;

  --thunder-border: #e1e5e9;
  --thunder-border-hover: #d3d8dd;

  /* 간격 */
  --thunder-spacing-xs: 4px;
  --thunder-spacing-sm: 8px;
  --thunder-spacing-md: 16px;
  --thunder-spacing-lg: 24px;
  --thunder-spacing-xl: 32px;
  --thunder-spacing-2xl: 48px;
  --thunder-spacing-3xl: 64px;

  /* 타이포그래피 */
  --thunder-font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    Oxygen, Ubuntu, sans-serif;
  --thunder-font-mono: "SF Mono", Monaco, Inconsolata, "Roboto Mono", Consolas,
    "Courier New", monospace;

  --thunder-font-size-xs: 10px;
  --thunder-font-size-sm: 12px;
  --thunder-font-size-base: 14px;
  --thunder-font-size-lg: 16px;
  --thunder-font-size-xl: 18px;
  --thunder-font-size-2xl: 24px;
  --thunder-font-size-3xl: 32px;
  --thunder-font-size-4xl: 48px;

  /* 둥근 모서리 */
  --thunder-radius-sm: 2px;
  --thunder-radius-md: 3px;
  --thunder-radius-lg: 4px;
  --thunder-radius-xl: 6px;
  --thunder-radius-2xl: 12px;
  --thunder-radius-full: 50%;

  /* 전환 효과 */
  --thunder-transition: all 0.15s ease;
  --thunder-transition-slow: all 0.2s ease;
}

/* Thunder Client 버튼 스타일 */
.thunder-btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: var(--thunder-spacing-sm) var(--thunder-spacing-lg);
  font-family: var(--thunder-font-family);
  font-size: var(--thunder-font-size-sm);
  font-weight: 600;
  line-height: 1;
  text-decoration: none;
  border: none;
  border-radius: var(--thunder-radius-md);
  cursor: pointer;
  transition: var(--thunder-transition-slow);
  white-space: nowrap;
  gap: var(--thunder-spacing-sm);
}

.thunder-btn-primary {
  background: var(--hapa-gradient);
  color: var(--thunder-text-light);
  box-shadow: var(--thunder-shadow-sm);
}

.thunder-btn-primary:hover {
  background: var(--hapa-gradient-hover);
  box-shadow: var(--thunder-shadow-md);
  transform: translateY(-1px);
}

.thunder-btn-primary:active {
  transform: translateY(0);
  box-shadow: var(--thunder-shadow-sm);
}

.thunder-btn-secondary {
  background: var(--thunder-bg-primary);
  color: var(--hapa-primary);
  border: 1px solid var(--thunder-border);
}

.thunder-btn-secondary:hover {
  background: var(--thunder-bg-secondary);
  border-color: var(--thunder-border-hover);
}

.thunder-btn-lg {
  padding: var(--thunder-spacing-md) var(--thunder-spacing-xl);
  font-size: var(--thunder-font-size-base);
}

/* Thunder Client 카드 스타일 */
.thunder-card {
  background: var(--thunder-bg-primary);
  border: 1px solid var(--thunder-border);
  border-radius: var(--thunder-radius-lg);
  box-shadow: var(--thunder-shadow-sm);
  transition: var(--thunder-transition);
}

.thunder-card:hover {
  box-shadow: var(--thunder-shadow-md);
  border-color: var(--thunder-border-hover);
}

.thunder-card-header {
  padding: var(--thunder-spacing-lg);
  border-bottom: 1px solid var(--thunder-border);
  background: var(--thunder-bg-secondary);
  border-radius: var(--thunder-radius-lg) var(--thunder-radius-lg) 0 0;
}

.thunder-card-body {
  padding: var(--thunder-spacing-lg);
}

/* Thunder Client 섹션 헤더 */
.thunder-section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--thunder-spacing-sm) var(--thunder-spacing-md);
  background: var(--thunder-bg-section);
  border: 1px solid var(--thunder-border);
  border-radius: var(--thunder-radius-md);
  font-size: var(--thunder-font-size-xs);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  color: var(--thunder-text-secondary);
  margin-bottom: var(--thunder-spacing-lg);
}

/* Thunder Client 아이콘 */
.thunder-icon {
  width: 16px;
  height: 16px;
  background: var(--hapa-gradient);
  border-radius: var(--thunder-radius-md);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: var(--thunder-text-light);
  font-size: var(--thunder-font-size-xs);
  font-weight: bold;
}

.thunder-icon-lg {
  width: 32px;
  height: 32px;
  font-size: var(--thunder-font-size-lg);
}

.thunder-icon-xl {
  width: 48px;
  height: 48px;
  font-size: var(--thunder-font-size-xl);
}

/* Thunder Client 상태 표시 */
.thunder-status {
  display: inline-flex;
  align-items: center;
  gap: var(--thunder-spacing-xs);
  font-size: var(--thunder-font-size-xs);
  color: var(--thunder-text-secondary);
}

.thunder-status-dot {
  width: 6px;
  height: 6px;
  border-radius: var(--thunder-radius-full);
  background-color: var(--hapa-accent);
  animation: thunder-pulse 2s infinite;
}

@keyframes thunder-pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

/* Thunder Client 탭 스타일 */
.thunder-tabs {
  display: flex;
  background: var(--thunder-bg-section);
  border: 1px solid var(--thunder-border);
  border-radius: var(--thunder-radius-md);
  overflow: hidden;
}

.thunder-tab {
  padding: var(--thunder-spacing-sm) var(--thunder-spacing-md);
  font-size: var(--thunder-font-size-sm);
  font-weight: 500;
  background: none;
  border: none;
  color: var(--thunder-text-secondary);
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: var(--thunder-transition);
  position: relative;
}

.thunder-tab:hover {
  color: var(--thunder-text-primary);
  background: var(--thunder-bg-primary);
}

.thunder-tab.active {
  color: var(--thunder-text-primary);
  background: var(--thunder-bg-primary);
  border-bottom-color: var(--hapa-primary);
}

/* Thunder Client 코드 블록 */
.thunder-code {
  background: var(--thunder-bg-dark);
  color: #d4d4d4;
  padding: var(--thunder-spacing-md);
  border-radius: var(--thunder-radius-md);
  font-family: var(--thunder-font-mono);
  font-size: var(--thunder-font-size-sm);
  line-height: 1.5;
  overflow-x: auto;
  white-space: pre-wrap;
}

/* Thunder Client 그리드 시스템 */
.thunder-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--thunder-spacing-lg);
}

.thunder-grid {
  display: grid;
  gap: var(--thunder-spacing-lg);
}

.thunder-grid-2 {
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.thunder-grid-3 {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
}

.thunder-grid-4 {
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
}

/* Thunder Client 유틸리티 클래스 */
.thunder-text-xs {
  font-size: var(--thunder-font-size-xs);
}
.thunder-text-sm {
  font-size: var(--thunder-font-size-sm);
}
.thunder-text-base {
  font-size: var(--thunder-font-size-base);
}
.thunder-text-lg {
  font-size: var(--thunder-font-size-lg);
}
.thunder-text-xl {
  font-size: var(--thunder-font-size-xl);
}
.thunder-text-2xl {
  font-size: var(--thunder-font-size-2xl);
}
.thunder-text-3xl {
  font-size: var(--thunder-font-size-3xl);
}
.thunder-text-4xl {
  font-size: var(--thunder-font-size-4xl);
}

.thunder-text-primary {
  color: var(--thunder-text-primary);
}
.thunder-text-secondary {
  color: var(--thunder-text-secondary);
}
.thunder-text-muted {
  color: var(--thunder-text-muted);
}
.thunder-text-light {
  color: var(--thunder-text-light);
}

.thunder-bg-primary {
  background-color: var(--thunder-bg-primary);
}
.thunder-bg-secondary {
  background-color: var(--thunder-bg-secondary);
}
.thunder-bg-dark {
  background-color: var(--thunder-bg-dark);
}

.thunder-rounded {
  border-radius: var(--thunder-radius-md);
}
.thunder-rounded-lg {
  border-radius: var(--thunder-radius-lg);
}
.thunder-rounded-xl {
  border-radius: var(--thunder-radius-xl);
}

.thunder-shadow {
  box-shadow: var(--thunder-shadow-sm);
}
.thunder-shadow-md {
  box-shadow: var(--thunder-shadow-md);
}
.thunder-shadow-lg {
  box-shadow: var(--thunder-shadow-lg);
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .thunder-container {
    padding: 0 var(--thunder-spacing-md);
  }

  .thunder-grid-2,
  .thunder-grid-3,
  .thunder-grid-4 {
    grid-template-columns: 1fr;
  }

  .thunder-btn-lg {
    padding: var(--thunder-spacing-sm) var(--thunder-spacing-lg);
    font-size: var(--thunder-font-size-sm);
  }
}
